import React from 'react';
import './styles/AboutTheProject.css';
import Header from './Header';

function AboutTheProject() {
    return (
        <>
            <Header />
            <div className="about-the-project">
                <h2>Sobre o projeto</h2>
                <p>Utilizando as frequncias das palavras, constri-se uma
                    rvore de Huffman. O algoritmo de Huffman atribui
                    cdigos binrios mais curtos para as palavras mais
                    frequentes e cdigos mais longos para as menos
                    frequentes, maximizando a eficincia da codificao.</p>
            </div>
        </>
    );
}

export default AboutTheProject;